@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .icon-tooltip {
        font-family: Source Sans Pro, sans-serif;
        @apply absolute w-auto px-3 py-2 ml-3 min-w-max left-16 bottom-2 rounded-xl shadow-md text-white bg-zinc-900 text-lg font-bold transition-all duration-100 scale-0 origin-left z-10;
    }
    .tech-tooltip {
        font-family: Source Sans Pro, sans-serif;
        @apply absolute w-auto px-2 py-1 top-11 left-[50%] translate-x-[-50%] text-center min-w-max rounded-xl shadow-md bg-slate-800 text-base font-bold transition-all duration-100 scale-0 origin-top z-10;
    }
}

body {
    background-color: #0f172a;
}