#projects {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    font-family: Work Sans, sans-serif
}

.project-card-container {
    gap: 15px;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 40px;
    margin-right: 40px;
    flex-wrap: wrap;
}

.project-card-container .project-card {
    position: relative;
    width: 250px;
    height: 375px;
    background: rgba(255, 255, 255, 0.05);
    margin: 20px;
    box-shadow: 0px 0px 20px black;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(10px);
    transition: 0.5s;
}

.project-card-container .project-card:hover {
    background: rgba(0, 0, 0, 0.5);
    box-shadow: none;
}

.project-card-container .project-card .project-content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: 0.5s;
    opacity: 0.5;
}

.project-card-container .project-card:hover .project-content {
    opacity: 1;
    transform: translateY(-20px);
}

.project-card-container .project-card .project-content .image-box {
    position: relative;
    width: 200px;
    height: 110px;
    overflow: hidden;
    border: 2px solid white;
}

.project-card-container .project-card .project-content .image-box img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-card-container .project-card .project-content .content-box {
    letter-spacing: 1px;
    text-transform: uppercase;
    text-align: center;
    font-family: "Work Sans", sans-serif;
    font-size: 20px;
    font-weight: 600;
    margin: 20px 0 10px;
    line-height: 1em;
}

.project-card-container .project-card .project-content .content-box span {
    font-size: 16px;
    font-weight: 300;
    text-transform: initial;
}

.project-card-container .project-card .technology-listing {
    position: absolute;
    bottom: 50px;
    display: flex;
}

.project-card-container .project-card .technology-listing li {
    list-style: none;
    margin: 0 10px;
    transform: translateY(40px);
    font-size: 35px;
    opacity: 0;
    transition-duration: 0.5s;
    transition-property: transform, opacity;
}

[data-index="1"] { transition-delay: 0.1s; }
[data-index="2"] { transition-delay: 0.2s; }
[data-index="3"] { transition-delay: 0.3s; }
[data-index="4"] { transition-delay: 0.4s; }

.project-card-container .project-card:hover .technology-listing li {
    transform: translateY(-10px);
    opacity: 1;
}