/* Ubuntu 300/400/500/700 */
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');
/* Work Sans 100/200/300/400/500/600/700/800/900 */
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
    --home: rgba(105, 55, 55);
    --home-transparent-1: rgba(105, 55, 55, 0.75);
    --home-transparent-2: rgba(105, 55, 55, 0.5);
    --theme-overlay: rgba(0, 0, 0, 0.25);
}

.caption {
    position: absolute;
    right: 20px;
    bottom: 10px;
    padding: 5px 10px;
    border-radius: 20px;
    font-family: "Ubuntu", sans-serif;
    font-size: 18px;
    color: aliceblue;
}

.title {
    font-family: Work Sans, sans-serif;
}

.caption:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

/* Personal QOL */
html {
    scroll-behavior: smooth;
}

body {
    overflow: overlay;
}

::-webkit-scrollbar {
    width: 16px;
}

::-webkit-scrollbar-track {
    box-shadow: 0 0 15px 15px transparent;
    border: solid 4px transparent;
}

::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 15px 15px gainsboro;
    border: solid 4px transparent;
    border-radius: 100px;
}

::-webkit-scrollbar-thumb:hover {
    box-shadow: inset 0 0 15px 15px skyblue;
}