.navigation {
    position: fixed;
    left: 50px;
    top: 50%;
    transform: translateY(-50%) translateX(-100px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;
    border-radius: 50px;
    opacity: 0;
    z-index: 9;
    animation: fade-nav-left 1s ease forwards;
}

.navigation:after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 50%;
    height: 100%;
    height: inherit;
    border-left: 2px solid rgba(255, 255, 255, 0.5);
}

.navigation a {
    position: relative;
    background-color: #18191c;
    border-radius: 30px;
}

.navigation a .icon.active-nav {
    position: relative;
    background-color: #3ba55d;
    border-radius: 20px;
    fill: white;
}

.icon {
    font-size: 60px;
    border-radius: 100%;
    padding: 10px;
    fill: #2ecc71;
    transition: fill 0.3s linear, background-color 0.3s linear, border-radius 0.25s ease-out;
}

.navigation .icon:hover {
    background-color: #3ba55d;
    fill: white;
    border-radius: 20px;
}

.navigation .icon.active-nav:hover {
    background-color: #2ecc71;
    fill: white;
}

@keyframes fade-nav-left {
    to {
        transform: translateY(-50%) translateX(0);
        opacity: 1;
    }
}

/* Tablets */
@media (max-width: 1100px) {
    .navigation {
        left: 50%;
        transform: translateX(-50%) translateY(100px);
        top: 85%;
        flex-direction: row;
        animation: fade-nav-bottom 1s ease forwards;
    }
    .navigation::after {
        display: none;
    }
    .navigation::before {
        position: absolute;
        top: calc(50% - 1px);
        right: 0;
        left: 0;
        content: "";
        background-color: rgba(255, 255, 255, 0.5);
        height: 2px;
    }

    @keyframes fade-nav-bottom {
        to {
            transform: translateX(-50%) translateY(0);
            opacity: 1;
        }
    }
}

@media (max-width: 650px) {
    .navigation {
        gap: 15px;
    }

    .icon {
        font-size: 50px;
        padding: 8px;
    }
}