.corner {
    position: fixed;
    top: 20px;
    right: 5%;
    justify-content: center;
    align-items: center;
    gap: 10px;
    transform: translateX(0) translateY(-100px);
    animation: fade-social-bottom 1s ease forwards;
}

.social-icon {
    font-size: 40px;
    background-color: rgba(0, 0, 0, 0.25);
    border-radius: 100%;
    padding: 5px;
    fill: rgba(255, 250, 240, 0.5);
    transition: fill 0.3s linear, background-color 0.3s linear, border-radius 0.25s ease-out;
}

@keyframes fade-social-bottom {
    to {
        transform: translateX(0) translateY(0);
        opacity: 1;
    }
}

.social-icon:hover {
    background-color: #206694;
    fill: white;
}